import CSSPlugin from 'gsap/CSSPlugin';
gsap.registerPlugin(ScrollTrigger, CSSPlugin, CSSRulePlugin)




function clickHandler(gsap, ScrollTrigger, context) {
    const ctx = gsap.context(() => {
        gsap.to(window, {
            scrollTo: () => `.footer`,
            duration: 1,
            delay: -1,
            ease: "power4",
        })
    }, context)
}

